h1 {
    font-size: 2rem;
}

/* Home Page */

.index .row {
    text-align: center;
    margin: 0 0;
    width: auto;
}

.index .photo-container {
    background-color: #25252528;
    padding: 1rem 0.5rem 12vw 0.5rem;
    margin: 3vw 0 2vw 0;
}

.index .photo-container img {
    width: 53vw;
    margin-top: -3vw;
    margin-bottom: -27vw;
    margin-left: 41vw;
}

.index .photo-container h1 {
    width: 100%;
    font-size: 12vw;
    font-family: "Quicksand", sans-serif;
    font-weight: bold;
    transform: skewX(-6deg);
    margin-top: -2.5vw;
    margin-left: 0;
    margin-bottom: -1rem;
    color: #926df7;
}

.index .photo-container h2 {
    font-size: 4vw;
    font-weight: bold;
    line-height: 0.8;
    color: #ffffff;
    margin-left: 1.5rem;
}

.index .photo-container h4 {
    font-size: 3vw;
    text-align: left;
    transform: skewX(-12deg);
    color: #FFFFFF;
    margin: 0.75vw auto;
}

.index .skills-container {
    background-color: #25252528;
    margin: 4vw auto;
    padding: 3rem 3rem;
}

.index .skills-container img {
    height: 10vw;
    margin: 2vw 1vw;
}

.index .skills-container .col:nth-child(1) img {
    height: 10.1vw;
}

.index .skills-container .col:nth-child(2) img {
    margin-top: 0.7vw;
    height: 12.7vw;
}

.index .skills-container .col:nth-child(4) img {
    margin-top: 2vw;
    height: 11vw;
}

.index .skills-container .col:nth-child(5) img {
    height: 16vw;
    margin-left: -3vw;
    margin-right: -10vw;
}

.index .skills-container .col:nth-child(6) img {
    margin-top: 3vw;
    height: 15vw;
}

.index .skills-container .col:nth-child(7) img {
    margin-top: 4vw;
    height: 15vw;
}

/* Projects Page */

.projects .row {
    margin: 0 auto;
    width: 97.5%;
    text-align: center;
}

.projects .project-box {
    text-align: center;
    background-color: #202020a2;
    box-shadow: 5px 5px #8181814b;
    margin: 2rem 1.2rem;
    padding: 0 2rem 0.5rem 2rem;
}

.projects img { 
    height: 10rem;
    margin: 0rem 0.5rem 2rem 0.5rem;
    float: center;
}

.projects p {
    color: rgb(190, 190, 190);
    font-size: 1rem;
    text-align: left;
}

.projects h2 {
    color: white;
    font-size: 2rem;
    width: inherit;
    margin: 2rem auto;
}

.projects h3 {
    color: rgb(255, 255, 255);
    font-size: 1.3rem;
    text-align: left;
}

.projects a p {
    color: rgb(86, 86, 246);
}

.projects a {
    outline: none;
}

.projects iframe {
    padding: 0 0;
    margin: 0 0 1rem 0;
}

/* Contacts Page */

.contact .row {
    width: 95%;
    margin: 0 auto;
    text-align: center;
}

.contact .contact-item {
    margin: 1.5rem 1.5rem;
    padding: 2rem 2rem;
    box-shadow: 5px 5px #8181814b;
    color: white;
    text-align: center;
}

.contact .box1 {
    background-color: #ff161663;
}
.contact .box2 {
    background-color: #64ffac78;
}
.contact .box3 {
    background-color: #7dc7f178;
}
.contact .box4 {
    background-color: #946dff9b;
}

.contact .box3 a, .contact .box4 a {
    color: #f6ec8cdc;
}

.contact img {
    width: 5rem;
    margin: 1rem auto;
}

.contact h2 {
    color: white;
    font-size: 1.3rem;
}

.title {
    max-width: 30rem;
    background-color: #90818136;
    box-shadow: 5px 5px #7a7a7a4b;
    padding: 1rem 1rem;
    margin: 1rem auto 2rem auto;
    color: white;
    max-height: 5rem;
    text-align: center;
}